import { BsFillCalendarFill } from "react-icons/bs";
import moment from "moment";
import { Card, Col } from "react-bootstrap";
import { useSelector } from "react-redux";

function PatientNotes(props) {
  const { getPatientData } = props;
  const { allPatientNotesData, isError, isLoading, isSuccess } = useSelector(
    (state) => state.patientNotes
  );

  const DateAndDetails = ({ doctor, date, note }) => {
    return (
      <div>
        <div className="d-flex justify-content-between align-items-center">
          <p className=" fs-5  fw-bold">{doctor}</p>
          <div className="d-flex">
            {date && (
              <>
                <BsFillCalendarFill size={20} />
                <p className="ps-2">{moment(date)?.format("DD/MM/YYYY")}</p>
              </>
            )}
          </div>
        </div>
        <p>Description: {note ? note : "N/A"}</p>
        <hr />
      </div>
    );
  };
  const DateAndDetailsList = ({ data }) => {
    return (
      <div>
        {data?.map(({ doctor, date, note }) => (
          <DateAndDetails key={date} doctor={doctor} date={date} note={note} />
        ))}
      </div>
    );
  };

  return (
    <Col lg={12}>
      <div className="patient-note">
        <Card>
          <Card.Img variant="top" className="Card-Image" />
          <Card.Body className="p-0">
            <div className="d-flex align-items-center upload_pic">
              <img
                src={
                  getPatientData?.imageUrl
                    ? getPatientData?.imageUrl
                    : `https://ui-avatars.com/api/?name=${`${getPatientData?.firstName} ${getPatientData?.lastName}  `}&background=000071&color=fff`
                }
                alt=""
                srcset=""
                className="img-fluid Profile-2 position-relative test-border"
              />
            </div>
            <Card.Title className="Personal-Information mb-5 ps-3 fw-bold">
              Notes
            </Card.Title>
            {isLoading ? (
              <p
                className="my-5 text-center fst-italic"
                style={{
                  color: "#999999",
                }}
              >
                Loading...
              </p>
            ) : isSuccess ? (
              <div className="px-4 patient-notes-data">
                {allPatientNotesData?.length !== 0 ? (
                  <DateAndDetailsList data={allPatientNotesData} />
                ) : (
                  <p className="text-center">No Data</p>
                )}
              </div>
            ) : isError ? (
              <p className="my-5 text-center text-danger fst-italic">
                Network Error
              </p>
            ) : null}
          </Card.Body>
        </Card>
      </div>
    </Col>
  );
}

export default PatientNotes;
