import { useEffect, useRef, useState } from "react";
import {
  Accordion,
  Card,
  Col,
  Dropdown,
  DropdownButton,
} from "react-bootstrap";
import { BsThreeDotsVertical } from "react-icons/bs";
import { PiDownloadSimpleBold } from "react-icons/pi";
import PrintComponents from "react-print-components";
import { useDispatch, useSelector } from "react-redux";
import {
  getDoctorAppointmentDetail,
  getPatientAppointmentNotesDetail,
} from "../../../../../app/features/appointment/appointment.slice";
import moment from "moment";
import MedicalCertificatePDF from "./ReferralNotesPDF/MedicalCertificatePDF";
import SpecialistLetterPDF from "./ReferralNotesPDF/SpecialistLetter";
import EmergencyLetterPDF from "./ReferralNotesPDF/EmergencyLetter";

function PatientReferralNote(props) {
  const { getPatientData } = props;
  const slotDescription = JSON.parse(localStorage.getItem("slotDescription"));
  const [singleDownLoad, setSingleDownload] = useState(null);
  const [emergencyData, setEmergencyData] = useState(null);
  const [medicalCertificateData, setMedicalCertificateData] = useState(null);
  const { patientAppointmentNotes } = useSelector(
    (state) => state?.appointment
  );
  const { profileNotesData, isLoading, isError, isSuccess } = useSelector(
    (state) => state?.patientRefNotes
  );

  const printButtonRef = useRef(null);
  const printButtonEmerRef = useRef(null);
  const printButtonMedRef = useRef(null);

  const dispatch = useDispatch();
  // const handleSpecialityFn = (data) => {
  //   setSingleDownload(data);
  //   printButtonRef?.current?.click();
  // };

  const handleSpecialityFn = (data) => {
    if (data) {
      setSingleDownload(data);
    }
  };
  const handleEmergencyFn = (data) => {
    setEmergencyData(data);
  };
  const handleMedicalFn = (data) => {
    setMedicalCertificateData(data);
  };
  useEffect(() => {
    if (singleDownLoad) {
      printButtonRef?.current?.click();
      setSingleDownload(null);
    } else if (emergencyData) {
      printButtonEmerRef?.current?.click();
      setEmergencyData(null);
    } else if (medicalCertificateData) {
      printButtonMedRef?.current?.click();
      setMedicalCertificateData(null);
    }
  }, [singleDownLoad, emergencyData, medicalCertificateData]);

  useEffect(() => {
    const finalData = {
      doctorId: slotDescription?.doctorId,
    };
    dispatch(getDoctorAppointmentDetail(finalData));
  }, [dispatch, slotDescription?.doctorId]);

  const handleAppointmentData = (id) => {
    const finalData = {
      appointmentId: id,
    };
    dispatch(getPatientAppointmentNotesDetail(finalData));
  };

  return (
    <Col lg={12}>
      <div className="referrel-note pt-ref-notes">
        <Card>
          <Card.Img variant="top" className="Card-Image" />
          <Card.Body className="p-0">
            <div className="d-flex align-items-center upload_pic">
              <img
                src={
                  getPatientData?.imageUrl
                    ? getPatientData?.imageUrl
                    : `https://ui-avatars.com/api/?name=${`${getPatientData?.firstName} ${getPatientData?.lastName}  `}&background=000071&color=fff`
                }
                alt=""
                srcset=""
                className="img-fluid Profile-2 position-relative test-border"
              />
            </div>

            <Card.Title className="Personal-Information mb-5 ps-3 fw-bold">
              Referral Notes
            </Card.Title>
            {isLoading ? (
              <p
                className="my-5 text-center fst-italic"
                style={{
                  color: "#999999",
                }}
              >
                Loading...
              </p>
            ) : isSuccess ? (
              <div className="referrel-note-data">
                {profileNotesData?.length > 0 ? (
                  profileNotesData?.map((data, index) => {
                    return (
                      <>
                        <Accordion className="py-3 mx-4">
                          <Accordion.Item eventKey="index">
                            <Accordion.Header
                              onClick={() =>
                                handleAppointmentData(data?.appointmentId)
                              }
                            >
                              <div className="d-flex justify-content-between fs-6 w-100">
                                <div>
                                  <span className="d-flex">
                                    <p className="fw-bold pe-1">
                                      Doctor Name:{" "}
                                    </p>
                                    <p>{data?.doctor}</p>
                                  </span>
                                  <span className="d-flex">
                                    <p className="mb-0 fw-bold pe-1">
                                      Start Time:{" "}
                                    </p>
                                    <p className="mb-0">{data?.startTime}</p>
                                  </span>
                                </div>
                                <div className="me-4">
                                  <span className="d-flex">
                                    <p className="fw-bold pe-1">
                                      Appointment Date:{" "}
                                    </p>
                                    <p>
                                      {moment(data?.appointmentDate)?.format(
                                        "DD/MM/YYYY"
                                      )}
                                    </p>
                                  </span>
                                  <span className="d-flex">
                                    <p className="mb-0 fw-bold pe-1">
                                      End Time:{" "}
                                    </p>
                                    <p className="mb-0">{data?.endTime}</p>
                                  </span>
                                </div>
                              </div>
                            </Accordion.Header>
                            <Accordion.Body className="py-4">
                              {patientAppointmentNotes?.specialistLetter ? (
                                <>
                                  <div className="d-flex justify-content-between">
                                    <h5 style={{ color: "#2269F2" }}>
                                      Specialist Letter
                                    </h5>
                                    <DropdownButton
                                      id="speciality-pdf"
                                      title={
                                        <BsThreeDotsVertical
                                          className="float-right color-99"
                                          size={25}
                                        />
                                      }
                                      size="sm"
                                      bsPrefix="moreIcon"
                                      className="moreIcon edit-dropdown"
                                      direction="left"
                                    >
                                      <div className="set-dropdown-menu">
                                        <Dropdown.Item className="d-flex align-items-center">
                                          <button
                                            className="bg-transparent border-0 p-0"
                                            onClick={() =>
                                              handleSpecialityFn(
                                                patientAppointmentNotes
                                              )
                                            }
                                          >
                                            <PiDownloadSimpleBold size={20} />
                                            Download PDF
                                          </button>
                                        </Dropdown.Item>
                                      </div>
                                    </DropdownButton>
                                  </div>
                                  <span className="d-flex">
                                    <div className="custom-padding pe-5">
                                      <p
                                        style={{ color: "#999999" }}
                                        className="mb-1"
                                      >
                                        Refferal Letter
                                      </p>
                                      <p>
                                        {patientAppointmentNotes
                                          ?.specialistLetter?.specialistLetter
                                          ? patientAppointmentNotes
                                              ?.specialistLetter
                                              ?.specialistLetter
                                          : "N/A"}
                                      </p>
                                    </div>
                                    <div className="d-flex custom-row">
                                      <div className="custom-padding">
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          To
                                        </p>
                                        <p>
                                          {patientAppointmentNotes
                                            ?.specialistLetter?.toDoctor
                                            ? patientAppointmentNotes
                                                ?.specialistLetter?.toDoctor
                                            : "N/A"}
                                        </p>
                                      </div>
                                    </div>
                                  </span>

                                  <div>
                                    {patientAppointmentNotes?.specialistLetter
                                      ?.appointmentDetail ? (
                                      <>
                                        <div>
                                          <p
                                            style={{ color: "#999999" }}
                                            className="mb-1"
                                          >
                                            Presenting Complaint
                                          </p>
                                          <p>
                                            {patientAppointmentNotes
                                              ?.specialistLetter
                                              ?.appointmentDetail
                                              ?.presentingComplaint
                                              ? patientAppointmentNotes
                                                  ?.specialistLetter
                                                  ?.appointmentDetail
                                                  ?.presentingComplaint
                                              : "N/A"}
                                          </p>
                                        </div>
                                        <div>
                                          <p
                                            style={{ color: "#999999" }}
                                            className="mb-1"
                                          >
                                            Past Medical History
                                          </p>
                                          <p>
                                            {patientAppointmentNotes
                                              ?.specialistLetter
                                              ?.appointmentDetail
                                              ?.pastMedicalHistory
                                              ? patientAppointmentNotes
                                                  ?.specialistLetter
                                                  ?.appointmentDetail
                                                  ?.pastMedicalHistory
                                              : "N/A"}
                                          </p>
                                        </div>
                                        <div>
                                          <p
                                            style={{ color: "#999999" }}
                                            className="mb-1"
                                          >
                                            Medications
                                          </p>
                                          <p>
                                            {patientAppointmentNotes
                                              ?.specialistLetter
                                              ?.appointmentDetail?.medications
                                              ? patientAppointmentNotes
                                                  ?.specialistLetter
                                                  ?.appointmentDetail
                                                  ?.medications
                                              : "N/A"}
                                          </p>
                                        </div>
                                        <div>
                                          <p
                                            style={{ color: "#999999" }}
                                            className="mb-1"
                                          >
                                            Allergies
                                          </p>
                                          <p>
                                            {patientAppointmentNotes
                                              ?.specialistLetter
                                              ?.appointmentDetail?.allergies
                                              ? patientAppointmentNotes
                                                  ?.specialistLetter
                                                  ?.appointmentDetail?.allergies
                                              : "N/A"}
                                          </p>
                                        </div>
                                        <div>
                                          <p
                                            style={{ color: "#999999" }}
                                            className="mb-1"
                                          >
                                            Family History
                                          </p>
                                          <p>
                                            {patientAppointmentNotes
                                              ?.specialistLetter
                                              ?.appointmentDetail?.familyHistory
                                              ? patientAppointmentNotes
                                                  ?.specialistLetter
                                                  ?.appointmentDetail
                                                  ?.familyHistory
                                              : "N/A"}
                                          </p>
                                        </div>

                                        <div>
                                          <p
                                            style={{ color: "#999999" }}
                                            className="mb-1"
                                          >
                                            Social History
                                          </p>
                                          <p>
                                            {patientAppointmentNotes
                                              ?.specialistLetter
                                              ?.appointmentDetail?.socialHistory
                                              ? patientAppointmentNotes
                                                  ?.specialistLetter
                                                  ?.appointmentDetail
                                                  ?.socialHistory
                                              : "N/A"}
                                          </p>
                                        </div>
                                        <div>
                                          <p
                                            style={{ color: "#999999" }}
                                            className="mb-1"
                                          >
                                            Suspected Diagnosis
                                          </p>
                                          <p>
                                            {patientAppointmentNotes
                                              ?.specialistLetter
                                              ?.appointmentDetail
                                              ?.suspectedDiagnosis
                                              ? patientAppointmentNotes
                                                  ?.specialistLetter
                                                  ?.appointmentDetail
                                                  ?.suspectedDiagnosis
                                              : "N/A"}
                                          </p>
                                        </div>
                                        <div>
                                          <p
                                            style={{ color: "#999999" }}
                                            className="mb-1"
                                          >
                                            Recommended Action
                                          </p>
                                          <p>
                                            {patientAppointmentNotes
                                              ?.specialistLetter
                                              ?.appointmentDetail
                                              ?.recommendedActions
                                              ? patientAppointmentNotes
                                                  ?.specialistLetter
                                                  ?.appointmentDetail
                                                  ?.recommendedActions
                                              : "N/A"}
                                          </p>
                                        </div>
                                        <div>
                                          <p
                                            style={{ color: "#999999" }}
                                            className="mb-1"
                                          >
                                            Sign off
                                          </p>
                                          <p>
                                            {patientAppointmentNotes
                                              ?.specialistLetter
                                              ?.appointmentDetail?.signOff
                                              ? patientAppointmentNotes
                                                  ?.specialistLetter
                                                  ?.appointmentDetail?.signOff
                                              : "N/A"}
                                          </p>
                                        </div>
                                      </>
                                    ) : (
                                      <>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Referral Notes
                                        </p>
                                        <p>
                                          {patientAppointmentNotes
                                            ?.specialistLetter?.referralNote
                                            ? patientAppointmentNotes
                                                ?.specialistLetter?.referralNote
                                            : "N/A"}
                                        </p>
                                      </>
                                    )}
                                  </div>
                                </>
                              ) : (
                                <p>No Speciality Letter Data</p>
                              )}
                              <hr />
                              {patientAppointmentNotes?.emergancyDepartmentLetter ? (
                                <>
                                  <div className="d-flex justify-content-between">
                                    <h5
                                      style={{ color: "#E31E27" }}
                                      className="mb-4"
                                    >
                                      Emergency Department Letter
                                    </h5>
                                    <DropdownButton
                                      id="emergency-pdf"
                                      title={
                                        <BsThreeDotsVertical
                                          className="float-right color-99"
                                          size={25}
                                        />
                                      }
                                      size="sm"
                                      bsPrefix="moreIcon"
                                      className="moreIcon edit-dropdown"
                                      direction="left"
                                    >
                                      <div className="set-dropdown-menu">
                                        <Dropdown.Item className="d-flex align-items-center">
                                          <button
                                            className="bg-transparent border-0 p-0"
                                            onClick={() =>
                                              handleEmergencyFn(
                                                patientAppointmentNotes
                                              )
                                            }
                                          >
                                            <PiDownloadSimpleBold size={20} />
                                            Download PDF
                                          </button>
                                        </Dropdown.Item>
                                      </div>
                                    </DropdownButton>
                                  </div>
                                  <span className="d-flex">
                                    <div className="custom-padding pe-5">
                                      <p
                                        style={{ color: "#999999" }}
                                        className="mb-1"
                                      >
                                        Refferal Letter
                                      </p>
                                      <p>
                                        {patientAppointmentNotes
                                          ?.emergancyDepartmentLetter
                                          ?.emergancyDepartmentLetter
                                          ? patientAppointmentNotes
                                              ?.emergancyDepartmentLetter
                                              ?.emergancyDepartmentLetter
                                          : "N/A"}
                                      </p>
                                    </div>
                                    <div className="d-flex custom-row">
                                      <div className="custom-padding">
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          To
                                        </p>
                                        <p>
                                          {patientAppointmentNotes
                                            ?.emergancyDepartmentLetter
                                            ?.toDoctor
                                            ? patientAppointmentNotes
                                                ?.emergancyDepartmentLetter
                                                ?.toDoctor
                                            : "N/A"}
                                        </p>
                                      </div>
                                    </div>
                                  </span>
                                  <div>
                                    {patientAppointmentNotes
                                      ?.emergancyDepartmentLetter
                                      ?.appointmentDetail ? (
                                      <>
                                        <div>
                                          <p
                                            style={{ color: "#999999" }}
                                            className="mb-1"
                                          >
                                            Presenting Complaint
                                          </p>
                                          <p>
                                            {patientAppointmentNotes
                                              ?.emergancyDepartmentLetter
                                              ?.appointmentDetail
                                              ?.presentingComplaint
                                              ? patientAppointmentNotes
                                                  ?.emergancyDepartmentLetter
                                                  ?.appointmentDetail
                                                  ?.presentingComplaint
                                              : "N/A"}
                                          </p>
                                        </div>
                                        <div>
                                          <p
                                            style={{ color: "#999999" }}
                                            className="mb-1"
                                          >
                                            Past Medical History
                                          </p>
                                          <p>
                                            {patientAppointmentNotes
                                              ?.emergancyDepartmentLetter
                                              ?.appointmentDetail
                                              ?.pastMedicalHistory
                                              ? patientAppointmentNotes
                                                  ?.emergancyDepartmentLetter
                                                  ?.appointmentDetail
                                                  ?.pastMedicalHistory
                                              : "N/A"}
                                          </p>
                                        </div>
                                        <div>
                                          <p
                                            style={{ color: "#999999" }}
                                            className="mb-1"
                                          >
                                            Medications
                                          </p>
                                          <p>
                                            {patientAppointmentNotes
                                              ?.emergancyDepartmentLetter
                                              ?.appointmentDetail?.medications
                                              ? patientAppointmentNotes
                                                  ?.emergancyDepartmentLetter
                                                  ?.appointmentDetail
                                                  ?.medications
                                              : "N/A"}
                                          </p>
                                        </div>
                                        <div>
                                          <p
                                            style={{ color: "#999999" }}
                                            className="mb-1"
                                          >
                                            Allergies
                                          </p>
                                          <p>
                                            {patientAppointmentNotes
                                              ?.emergancyDepartmentLetter
                                              ?.appointmentDetail?.allergies
                                              ? patientAppointmentNotes
                                                  ?.emergancyDepartmentLetter
                                                  ?.appointmentDetail?.allergies
                                              : "N/A"}
                                          </p>
                                        </div>
                                        <div>
                                          <p
                                            style={{ color: "#999999" }}
                                            className="mb-1"
                                          >
                                            Family History
                                          </p>
                                          <p>
                                            {patientAppointmentNotes
                                              ?.emergancyDepartmentLetter
                                              ?.appointmentDetail?.familyHistory
                                              ? patientAppointmentNotes
                                                  ?.emergancyDepartmentLetter
                                                  ?.appointmentDetail
                                                  ?.familyHistory
                                              : "N/A"}
                                          </p>
                                        </div>

                                        <div>
                                          <p
                                            style={{ color: "#999999" }}
                                            className="mb-1"
                                          >
                                            Social History
                                          </p>
                                          <p>
                                            {patientAppointmentNotes
                                              ?.emergancyDepartmentLetter
                                              ?.appointmentDetail?.socialHistory
                                              ? patientAppointmentNotes
                                                  ?.emergancyDepartmentLetter
                                                  ?.appointmentDetail
                                                  ?.socialHistory
                                              : "N/A"}
                                          </p>
                                        </div>
                                        <div>
                                          <p
                                            style={{ color: "#999999" }}
                                            className="mb-1"
                                          >
                                            Suspected Diagnosis
                                          </p>
                                          <p>
                                            {patientAppointmentNotes
                                              ?.emergancyDepartmentLetter
                                              ?.appointmentDetail
                                              ?.suspectedDiagnosis
                                              ? patientAppointmentNotes
                                                  ?.emergancyDepartmentLetter
                                                  ?.appointmentDetail
                                                  ?.suspectedDiagnosis
                                              : "N/A"}
                                          </p>
                                        </div>
                                        <div>
                                          <p
                                            style={{ color: "#999999" }}
                                            className="mb-1"
                                          >
                                            Recommended Action
                                          </p>
                                          <p>
                                            {patientAppointmentNotes
                                              ?.emergancyDepartmentLetter
                                              ?.appointmentDetail
                                              ?.recommendedActions
                                              ? patientAppointmentNotes
                                                  ?.emergancyDepartmentLetter
                                                  ?.appointmentDetail
                                                  ?.recommendedActions
                                              : "N/A"}
                                          </p>
                                        </div>
                                        <div>
                                          <p
                                            style={{ color: "#999999" }}
                                            className="mb-1"
                                          >
                                            Sign off
                                          </p>
                                          <p>
                                            {patientAppointmentNotes
                                              ?.emergancyDepartmentLetter
                                              ?.appointmentDetail?.signOff
                                              ? patientAppointmentNotes
                                                  ?.emergancyDepartmentLetter
                                                  ?.appointmentDetail?.signOff
                                              : "N/A"}
                                          </p>
                                        </div>
                                      </>
                                    ) : (
                                      <>
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          Referral Notes
                                        </p>
                                        <p>
                                          {patientAppointmentNotes
                                            ?.emergancyDepartmentLetter
                                            ?.referralNote
                                            ? patientAppointmentNotes
                                                ?.emergancyDepartmentLetter
                                                ?.referralNote
                                            : "N/A"}
                                        </p>
                                      </>
                                    )}
                                  </div>
                                </>
                              ) : (
                                <p>No Emergency Letter Data</p>
                              )}
                              <hr />
                              {patientAppointmentNotes?.medicalCertificate ? (
                                <>
                                  <div className="d-flex justify-content-between">
                                    <h5
                                      style={{ color: "#44BC19" }}
                                      className="mb-4"
                                    >
                                      Medical Certificate
                                    </h5>
                                    <DropdownButton
                                      id="medical-certificate-pdf"
                                      title={
                                        <BsThreeDotsVertical
                                          className="float-right color-99"
                                          size={25}
                                        />
                                      }
                                      size="sm"
                                      bsPrefix="moreIcon"
                                      className="moreIcon edit-dropdown"
                                      direction="left"
                                    >
                                      <div className="set-dropdown-menu">
                                        <Dropdown.Item className="d-flex align-items-center">
                                          <button
                                            className="bg-transparent border-0 p-0"
                                            onClick={() =>
                                              handleMedicalFn(
                                                patientAppointmentNotes
                                              )
                                            }
                                          >
                                            <PiDownloadSimpleBold size={20} />
                                            Download PDF
                                          </button>
                                        </Dropdown.Item>
                                      </div>
                                    </DropdownButton>
                                  </div>
                                  <span className="d-flex">
                                    <div className="custom-padding pe-5">
                                      <p
                                        style={{ color: "#999999" }}
                                        className="mb-1"
                                      >
                                        From
                                      </p>
                                      <p>
                                        {moment(
                                          patientAppointmentNotes
                                            ?.medicalCertificate?.fromDate
                                        )?.format("DD/MM/YYYY") || "N/A"}
                                      </p>
                                    </div>
                                    <div className="d-flex custom-row">
                                      <div className="custom-padding">
                                        <p
                                          style={{ color: "#999999" }}
                                          className="mb-1"
                                        >
                                          To
                                        </p>
                                        <p>
                                          {moment(
                                            patientAppointmentNotes
                                              ?.medicalCertificate?.toDate
                                          )?.format("DD/MM/YYYY") || "N/A"}
                                        </p>
                                      </div>
                                    </div>
                                  </span>
                                  <div className="custom-padding">
                                    <p
                                      style={{ color: "#999999" }}
                                      className="mb-1"
                                    >
                                      Unable To Attend
                                    </p>
                                    <p>
                                      {patientAppointmentNotes
                                        ?.medicalCertificate
                                        ?.unabletoAttendField
                                        ? patientAppointmentNotes
                                            ?.medicalCertificate
                                            ?.unabletoAttendField
                                        : "N/A"}
                                    </p>
                                  </div>
                                  <div className="custom-padding">
                                    <p
                                      style={{ color: "#999999" }}
                                      className="mb-1"
                                    >
                                      Suffering From
                                    </p>
                                    <p>
                                      {patientAppointmentNotes
                                        ?.medicalCertificate?.sufferingFrom
                                        ? patientAppointmentNotes
                                            ?.medicalCertificate?.sufferingFrom
                                        : "N/A"}
                                    </p>
                                  </div>
                                </>
                              ) : (
                                <p>No Medical Certificate Data</p>
                              )}
                            </Accordion.Body>
                          </Accordion.Item>
                        </Accordion>
                      </>
                    );
                  })
                ) : (
                  <p className="text-center">No Data</p>
                )}
              </div>
            ) : isError ? (
              <p className="my-5 text-center text-danger fst-italic">
                Network Error
              </p>
            ) : null}
          </Card.Body>
        </Card>
      </div>

      <PrintComponents
        trigger={
          <button
            ref={printButtonRef}
            className="bg-transparent border-0 p-0 d-none"
          >
            <PiDownloadSimpleBold size={20} />
            Download PDF
          </button>
        }
      >
        <SpecialistLetterPDF
          stateData={singleDownLoad}
          personalDetails={profileNotesData}
          signature={patientAppointmentNotes}
        />
      </PrintComponents>
      <PrintComponents
        trigger={
          <button
            ref={printButtonEmerRef}
            className="bg-transparent border-0 p-0 d-none"
          >
            <PiDownloadSimpleBold size={20} />
            Download PDF
          </button>
        }
      >
        <EmergencyLetterPDF
          stateData={emergencyData}
          personalDetails={profileNotesData}
          signature={patientAppointmentNotes}
        />
      </PrintComponents>
      <PrintComponents
        trigger={
          <button
            ref={printButtonMedRef}
            className="bg-transparent border-0 p-0 d-none"
          >
            <PiDownloadSimpleBold size={20} />
            Download PDF
          </button>
        }
      >
        <MedicalCertificatePDF
          MedicalCertificateData={medicalCertificateData?.medicalCertificate}
          personalDetails={profileNotesData}
          signature={patientAppointmentNotes}
        />
      </PrintComponents>
    </Col>
  );
}

export default PatientReferralNote;
